import React, { useState } from 'react';
import DatePicker from 'react-datepicker';
import { ko } from 'date-fns/locale';
import 'react-datepicker/dist/react-datepicker.css';
import './StudentList.css'; //  CSS   .

function StudentList({ students, onDeleteStudent, onUpdateStudent }) {
  const [selectedStudent, setSelectedStudent] = useState(null);
  const [schedule, setSchedule] = useState(null);
  const [lessonContent, setLessonContent] = useState('');
  const [editingSchedule, setEditingSchedule] = useState(null);

  const handleDateChange = (date) => {
    setSchedule(date);
  };

  const handleAddSchedule = (student) => {
    if (schedule && lessonContent.trim() !== '') {
      const updatedSchedules = [...student.schedules, { date: schedule, content: lessonContent }];
      onUpdateStudent(student.id, { ...student, schedules: updatedSchedules });
      setSchedule(null);
      setLessonContent('');
      setSelectedStudent(null);
    } else {
      alert('    .');
    }
  };

  const handleEditSchedule = (student, scheduleIndex) => {
    setEditingSchedule({ studentId: student.id, index: scheduleIndex });
    setSchedule(new Date(student.schedules[scheduleIndex].date));
    setLessonContent(student.schedules[scheduleIndex].content);
  };

  const handleUpdateSchedule = (student) => {
    if (schedule && lessonContent.trim() !== '') {
      const updatedSchedules = [...student.schedules];
      updatedSchedules[editingSchedule.index] = { date: schedule, content: lessonContent };
      onUpdateStudent(student.id, { ...student, schedules: updatedSchedules });
      setEditingSchedule(null);
      setSchedule(null);
      setLessonContent('');
    } else {
      alert('    .');
    }
  };

  return (
    <div>
      <h2> </h2>
      {students.length === 0 ? (
        <p>  .</p>
      ) : (
        <table>
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th> </th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {students.map((student) => (
              <tr key={student.id}>
                <td>{student.name}</td>
                <td>{student.age}</td>
                <td>{student.phone}</td>
                <td>{student.email}</td>
                <td>
                  {student.schedules.map((schedule, index) => (
                    <div key={index}>
                      <div>{new Date(schedule.date).toLocaleString()}</div>
                      <div>{schedule.content}</div>
                      <button onClick={() => handleEditSchedule(student, index)}></button>
                    </div>
                  ))}
                  {(selectedStudent === student.id || editingSchedule?.studentId === student.id) && (
                    <div className="add-lesson">
                      <div className="date-picker-container">
                        <label htmlFor={`date-picker-${student.id}`}>  :</label>
                        <DatePicker
                          id={`date-picker-${student.id}`}
                          selected={schedule}
                          onChange={handleDateChange}
                          showTimeSelect
                          dateFormat="yyyy MM dd HH:mm"
                          placeholderText="  "
                          className="date-picker-input"
                          timeFormat="HH:mm"
                          timeIntervals={15}
                          timeCaption=""
                          locale={ko}
                        />
                      </div>
                      <div className="lesson-content-container">
                        <label htmlFor={`lesson-content-${student.id}`}> :</label>
                        <input
                          id={`lesson-content-${student.id}`}
                          type="text"
                          value={lessonContent}
                          onChange={(e) => setLessonContent(e.target.value)}
                          placeholder="  "
                        />
                      </div>
                      {editingSchedule ? (
                        <button onClick={() => handleUpdateSchedule(student)}> </button>
                      ) : (
                        <button onClick={() => handleAddSchedule(student)}></button>
                      )}
                    </div>
                  )}
                </td>
                <td>
                  <button onClick={() => setSelectedStudent(student.id)}> </button>
                  <button onClick={() => onDeleteStudent(student.id)} className="delete-btn"></button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
}

export default StudentList;
